section.module-top {
  background-color: #fff;
  width: 360px;
  margin-left: 1em;
  margin-top: 1em;
}
section.module-top.out {
  transform: translateX(-380px);
}
section.module-top .md-subheader {
  font-size: 1.4em;
}
section.module-top md-list md-list-item {
  border-bottom: 1px solid #d7dddd;
}
section.module-top md-list md-list-item:last-child {
  border-bottom: 0px;
}
section.module-top md-list .md-button {
  border-radius: 0;
}
section.module-top md-list .md-button .md-ripple-container {
  border-radius: 0;
}
section.module-top .upload-container {
  text-align: center;
}
section.table {
  background-color: rgba(255, 255, 255, 0.2);
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  height: calc(100%);
  background: #fff;
}
section.table md-data-table-toolbar {
  position: relative;
  background: #006bb9;
  color: #fff;
}
section.table md-data-table-toolbar * {
  fill: #fff;
}
section.table md-data-table-toolbar md-input-container {
  padding-bottom: .5em;
}
section.table md-data-table-container {
  font-family: RobotoDraft, Roboto, 'Helvetica Neue', sans-serif;
  margin-bottom: 56px;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th {
  color: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed {
  position: relative;
  padding-left: 3em !important;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon {
  opacity: 0;
  transition: all .3s ease;
  position: absolute;
  top: 1.3em;
  left: 0em;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon path {
  fill: #ccc;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon:hover path {
  fill: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed.globe span {
  opacity: 1;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed.globe span path {
  fill: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed:hover span.icon {
  opacity: 1;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done,
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done.md-active {
  color: green !important;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done span:hover path,
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done.md-active span:hover path {
  fill: green !important;
}
section.table md-data-table-container table[md-data-table] > tbody > tr {
  height: 2.5em;
}
section.table md-data-table-container table[md-data-table] > tbody > tr.md-warn {
  background-color: rgba(244, 67, 54, 0.2);
}
section.table md-data-table-container table[md-data-table] > tbody > tr td.md-warn::after {
  content: 'Data missing';
  color: red;
  width: 100%;
  display: inline-block;
  line-height: 3em;
}
section.table md-data-table-pagination {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: #fff;
}
